export default class Node {
  constructor(
    tree_item_visible,
    tree_no_null,
    tree_loading,
    level,
    tree_checked,
    parent
  ) {
    this.tree_item_visible = tree_item_visible
    this.tree_no_null = tree_no_null
    this.tree_loading = tree_loading
    this.level = level
    this.tree_checked = tree_checked
    this.parent = parent
  }
}



